/*
==========================
INPUT & INPUT BTN & ERROR
==========================
*/
#query-input {
  margin: 0 auto;
  max-width: 1080px;
  width: 50vw;
  padding: 8px;
  border: none;
  box-shadow: 3px 3px 6px #e6e6e6;
  color: #747474; }
  #query-input::placeholder {
    color: #a1a1a1; }

.query-btn {
  border: none;
  color: white;
  background-color: #15cf89;
  box-shadow: 3px 3px 6px #e6e6e6;
  padding: 8px 16px;
  font-weight: 600; }

/*
==========================
MEDIA QUERY
==========================
*/
@media (max-width: 550px) {
  #query-input {
    width: 60%; } }

@media (max-width: 340px) {
  #query-input {
    width: 100%; }
  .query-btn {
    width: 100%; } }
