/*
==========================
SELECTED TITLES
==========================
*/
.selected-titles-container {
  margin: 0 auto;
  max-width: 1080px;
  width: 50vw;
  padding: 8px;
  border: none;
  box-shadow: 3px 3px 6px #e6e6e6;
  color: #747474; }
  .selected-titles-container::placeholder {
    color: #a1a1a1; }

h2 {
  color: #a1a1a1; }

.selected-titles {
  margin: 0 auto;
  max-width: 1080px;
  width: 50vw;
  padding: 8px;
  border: none;
  color: #747474;
  opacity: 0;
  animation: fadeIn 1.5s 1;
  animation-fill-mode: forwards; }
  .selected-titles::placeholder {
    color: #a1a1a1; }

@keyframes fadeIn {
  0% {
    opacity: 0;
    height: 0px; }
  100% {
    opacity: 1;
    height: 100%; } }

/*
==========================
MEDIA QUERY
==========================
*/
@media (max-width: 550px) {
  #query-input {
    width: 60%; } }

@media (max-width: 340px) {
  #query-input {
    width: 100%; }
  .query-btn {
    width: 100%; } }
